import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Teams extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadTeams();
    }

	loadTeams() {
		const { teamStore } = this.props.stores;
		teamStore.load();
	}

	render() {
		const { teamStore, userStore } = this.props.stores;
		const { teams } = teamStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h5>Teamene dine - {teams.length} stk</h5>
						<div class='row'>
							{teams && teams.map(team => (
								<div class='col-12 col-sm-6 col-lg-2 mt-4 d-flex'>
									{/* {JSON.stringify(team, null, 4)} */}
									<div class={`text-center border rounded-circle imageRounded`} style={team.image ? `background-image: url("${team.image.s3SmallLink}");` : ''}>
										{!team.image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3'>
										<span class='float-right'>
											{team.birth && <span class='mr-2'>{util.age(team.birth, team.deceased)} Ã¥r</span>}
											{team.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
										</span>
										<a class='stretched-link' href={`/teams/${team.id}`}>
											<h5>{team.name}</h5>
										</a>
										<small>
											{team && team.dogs && team.dogs.length} <i class='fas fa-dog mr-4' />
											{team && team.members && team.members.length} <i class='fas fa-user' />
										</small>
									</div>
								</div>
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/teams/new'
							>
								<i class='fas fa-plus' /> Legg til team
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Teams;
